import React, { useRef, useEffect } from "react";
import { SceneBg, ParallaxWrapper } from "../ui/modules";
import Parallax from "parallax-js";

export const ParallaxComponent = ({ bg, setBgLoaded }) => {
  const wrapperEl = useRef(null);

  // if (!bg) setBgLoaded(true);

  useEffect(() => {
    const parallax = new Parallax(wrapperEl.current, {});
    return () => {
      parallax.disable();
    };
  }, []);

  return (
    <ParallaxWrapper ref={wrapperEl}>
      {bg && (
        <SceneBg src={bg} data-depth="0.10" onLoad={() => setBgLoaded(true)} />
      )}
    </ParallaxWrapper>
  );
};
