import React, { useState, useEffect } from "react";
import DirectionsRunIcon from "@material-ui/icons/DirectionsRun";
import styled from "styled-components";
import { Text } from "../ui/elements";
import { ProgressBar } from "../ui/modules/ProgressBar";
import { withRouter } from "react-router-dom";

// const withRunner = props => Component => {
//   const [progress, setProgress] = useState(50);

//   return <Component progress={progress} {...props} />;
// };

export const ReactRunner = withRouter(
  styled(({ className, success, fail }) => {
    const [progress, setProgress] = useState(50);

    // let interval;
    // useEffect(() => {
    //   // eslint-disable-next-line react-hooks/exhaustive-deps
    //   const interval = setInterval(() => {
    //     setProgress(p => p - 0.005);
    //   }, 100);
    //   // return () => clearInterval(interval);
    // }, []);
    useEffect(() => {
      const interval = setInterval(() => {
        setProgress(progress => progress - 0.05);
      }, 20);
      return () => clearInterval(interval);
    }, []);
    // useEffect(() => {
    //   const interval = setInterval(() => {
    //     setProgress(p => p - 0.01);
    //   }, 100);
    //   return () => clearInterval(interval);
    // }, []);

    // console.log(progress);

    if (progress > 100) {
      success();
      setProgress(100);
    }
    if (progress < 0) {
      fail();
      setProgress(0);
    }

    return (
      <div className={`ReactRunner ${className}`}>
        <Text>Бегите!!!</Text>
        <ProgressBar progress={progress} />
        <DirectionsRunIcon
          onClick={() => {
            setProgress(p => p + 3);
            // clearInterval(interval);
          }}
        />
      </div>
    );
  })`
    ${Text} {
      color: ${props => props.theme.colors.light};
      margin-bottom: 3rem;
      margin-top: 0;
    }
    display: flex;
    justify-content: center;
    height: 100%;
    align-items: center;
    flex-direction: column;
    .MuiSvgIcon-root {
      margin-top: 6rem;
      color: ${props => props.theme.colors.light};
      font-size: 10rem;
      transition: 0.2s;
      cursor: pointer;
      &:hover {
        transform: scale(1.05);
      }
      &:active {
        transform: scale(1.5);
      }
    }
  `
);
